@import url(https://fonts.googleapis.com/css?family=Roboto:400,100,900);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,100,900);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,100,900);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,100,900);
.App {
  min-height: 100vh;
  min-width: 100vw;
  text-align: center;
  background-color: #282c34;
  background-size: cover;
  overflow: hidden;
  
}

.App-link {
  color: #61dafb;
}

/*"Normal Map Generator" text*/
#title{
  font-size: calc(50px + 2vmin);
  word-spacing: absolute;
  text-align: center;
  text-decoration: none;
  display: block;
}


/*Defines the header block where "Normal Map Generator" text sits*/
.header {
 
  height: -webkit-fit-content;
 
  height: -moz-fit-content;
 
  height: fit-content;
  align-items: center;
  flex-direction: column;
  justify-content: top;
  font-size: calc(10px + 2vmin);
  color: white;

}


.App-link {
  color: #61dafb;
}

.main_body {
  height: 100%; 
}


/*header specific css*/
.header_column{
  float:left;

}

.left, .right{
  width: 25%;
  padding-bottom: 500em;
  margin-bottom: -500em;
  margin-top: 30px;
  align-items: center;
  

}

.middle{
  width: 50%;
  padding-bottom: 500em;
  margin-bottom: -500em;
}

.header_row:after{
  content: "";
  display: table;
  clear: both;
  
}

@media screen and (max-width: 981px){
  .header_column{
    width: 100%;
  }
}
.main-container {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  text-align:center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

#splash-title{
  font-size: 70px;
  word-spacing: absolute;
  text-align: center;
  text-decoration: none;
  display: block;
  margin-bottom: 100px; /* between paragraphs */
}


#splash-text {
  text-align: center;
  text-decoration: none;
  display: block;
  font-family: 'Trebuchet MS', sans-serif, Helvetica, sans-serif;
  line-height: 0px;   /* within paragraph */
  margin-bottom: 100px; /* between paragraphs */
  }

h1 {
  position: fixed;
  margin-top: -700px;
  word-spacing: absolute;
  text-align: center;
  text-decoration: none;
  display: block;
  font-family: 'Trebuchet MS', sans-serif, Helvetica, sans-serif;
  line-height: 32px;   /* within paragraph */
  margin-bottom: 50px; /* between paragraphs */
}

h1:hover{
  cursor: default;
}


/*Main css styling for all button elements*/
#button-container{
  position:center;
  font-family: 'Trebuchet MS', sans-serif, Helvetica, sans-serif;
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  border-radius: 15px;
  transition-duration: .5s;
  background-color: #04AA6D; 
}


#button-container:hover {
  background-color: #3edd9b; 
  box-shadow: 0px 0px 8px 1px #9BFFFB;
}

#button-display{
  display:none;
}


/*Affixes button to the bottom of the screen*/
.BtnBtm{
  position: static;
  bottom: 10px;
  margin: 0 auto;
  z-index: 10;

}

.BtnBtm > button {
  margin: 0px 45px;
  padding: 0px 50px;
}








.back-button {
  left: -5%;
  width: 150px;

}

.back-button:hover {
  
  background-color: #3edd9b; 
  box-shadow: 0px 0px 8px 1px #9BFFFB;

}

.back-button:hover:before{
  content: "Go Back";

}

button:hover span {
  display: none
}

.continue{
  border-radius: 15px;
  z-index: 10;
}

#cropper {
  margin-bottom: 75px;
  margin-top: 50px;
  line-height: 32px;
  font-size: calc(1px + 2vmin);
  
}

.centered {
  position: relative;
  height: 80vh;
  display: grid;
  justify-content: center;
  align-items: center;
}

.continueBtnBtm{
  display: flex;
  position: absolute;
  bottom: 50px;
  z-index: 10;
}


.continueBtnBtm > button {
  margin: 0px 45px;
  padding: 0px 50px;
  z-index: 10;
}

.model-picker{
  background-color: #04AA6D;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  width:7vw;
  height:30px;
  padding-bottom:6px;
  padding-top:15px;
  margin:0;
  margin-top:-20px;
  line-height: normal;
  box-sizing:content-box;
  font-family: 'Trebuchet MS', sans-serif, Helvetica, sans-serif;
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
}

.custom-loader{
  margin:0;
}

#top-selector{
  padding-top: 13px;
  padding-bottom: 17px;
  padding-bottom:16px;
}

#left-selector{
  border-top-right-radius: 0;
  border-bottom-left-radius: 15px;
  padding-bottom:16px;
}

#right-selector{
  border-top-left-radius: 0;
  border-bottom-right-radius: 15px;
  padding-bottom:16px;
}

#center-selector{
  border-radius: 0;
  padding-bottom:16px;
}

#model-selector-container{
  position:absolute;
  width:100%;
  top: -75px;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}

#three-container{
  background-color: rgba(240, 248, 255, 0);
  max-width: 800px;
  max-height: 800px;
  height: 30vw;
  width: 30vw;
  position: relative;
}

#slider-container{
  min-width: 260px;
  width: 100%;
}

.three-canvas{
  margin-right: auto;
  margin-left: auto;
  height: 100%;
  max-height: 800px;
  max-width: 800px;
}

#highres-canvas{
  display:none;
}

#normal-canvas{
  /* margin-right: auto;
  margin-left: auto;
  display: inline-block; */
  max-width: 800px;
  max-height: 600px;
  height: 100%;
  width: 100%;
}

#mapcontainer{
  align-self: center;
  display: block;
  text-align: center;
}

.grid_items {
  position: relative;
  display: grid;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.grid_items {
  align-items: center;
  justify-items: center;
  grid-template-columns: 40% 20% 40%;
  height: 80vh;
  grid-row-gap: 50px;
  margin-left: 30px;
  margin-right: 30px;
}

.grid_item{
  width:80%;
  padding: 0px 40px;
  /* height: 100%; */
}

.grid_items > div:nth-child(3n + 2) {
  background-color: rgb(177, 187, 197);
  border-radius: 40px;
  padding: 50px;
}


@media only screen and (max-width: 980px) {
  .grid_items {
    justify-content: center;
    grid-template-columns: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    grid-row-gap: 40px;
  }
  
  .grid_item{
    width:auto;
  }

  #three-container{
    background-color: rgba(240, 248, 255, 0);
    max-width: 800px;
    max-height: 800px;
    height: 50vw;
    width: 74vw;
    position: relative;
    margin-top:80px;
  }

  .model-picker{
    width:25%;
  }

  #slider-container{
    width: 70%;
  }

  #title{
    font-size: min(10vw, calc(50px + 2vmin));
  }
}


